body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.left-icon {
  margin-right: 8px;
}

.app {
  width: 80%;
  margin: 48px auto;
}

.app.iframe-pls {
  width: 100%;
  margin: 0;
}

@media (max-width: 700px) {
  .app {
    width: auto;
    margin: 16px;
  }
}

.root {
  padding: 2px 4px;
  display: flex;
  align-items: center;
  width: 100%;
}

.input {
  flex: 1 1;
  margin-left: 16px;
}

.loading {
  margin: 12px;
}

.contact {
  display: flex;
  flex-direction: row;
  align-items: center;
}

.image {
  max-height: 220px;
  width: auto;
  height: auto;
}

.template {
  white-space: pre-wrap;
}

.email-array {
  display: flex;
  flex-wrap: nowrap;
  overflow-x: auto;
  -webkit-overflow-scrolling: touch;
}

.email-array::-webkit-scrollbar {
   display: none;
 }

.email-array > * {
  flex: 0 0 auto;
}

@media (max-width: 600px) {
  .contact {
    display: flex;
    flex-direction: column;
  }

  .image {
    width: 100%;
    max-height: none;
  }
}

.button-img {
  padding: 2px;
}

